@charset "UTF-8";
.container-special {
  width: 960px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

.hero-flex {
  display: flex;
  flex-direction: column;
  background-color: #25807F;
}
.hero-flex__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hero-flex__item:last-child {
  background-color: #fff;
}

.list-info {
  background-color: #25807F;
  width: 55%;
  list-style: none;
  padding: 20px 10px;
  margin: 0;
}
.list-info__item {
  text-transform: uppercase;
  color: #fff;
  font-size: 16px;
  font-weight: 700;
}

.logo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(../../img/logofon.gif);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top left;
  width: 45%;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 15% 100%);
  clip-path: polygon(0 0, 100% 0, 100% 100%, 15% 100%);
  border: 1px solid #fff;
}

.logo {
  padding: 15px;
}

.group-info {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 62.5%;
}

.address-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #25807F;
  padding: 10px 0;
  width: 37.5%;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 5% 100%);
  clip-path: polygon(0 0, 100% 0, 100% 100%, 5% 100%);
}

.icons {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 5px 0;
}

.icon {
  margin-right: 5px;
}

.numbers {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding-left: 0;
}
.numbers::before {
  content: "Тел:";
  font-weight: 700;
}

.number {
  padding: 5px;
  font-size: 16px;
  font-weight: 700;
}
.number:first-child::after {
  content: ",";
}

.email {
  font-weight: 700;
  color: #25807F;
}

.address {
  background-color: #25807F;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  margin: 0;
}

@media screen and (max-width: 700px) {
  .container-special {
    width: 680px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
  }

  .hero-flex {
    flex-direction: row-reverse;
    justify-content: space-between;
  }
  .hero-flex__item {
    width: 50%;
  }
  .hero-flex__item:first-child {
    justify-content: end;
  }
  .hero-flex__item:last-child {
    justify-content: center;
    background-color: #25807F;
    color: #fff;
  }

  .email {
    color: #fff;
  }

  .list-info,
.address-wrap,
.icons {
    display: none;
  }

  .logo-wrap {
    -webkit-clip-path: none;
    clip-path: none;
    width: 100%;
  }

  .group-info {
    flex-direction: column;
  }

  .numbers {
    flex-direction: column;
  }
  .numbers::before {
    content: none;
  }

  .number {
    font-size: 16px;
  }
  .number:first-child::after {
    content: none;
  }
}